import React, {Component} from "react";
import Joi from "joi-browser";
import {Input} from "./input";

export default class Form extends Component {
   state = {
      data:{},
      errors:{}
   }

   validate = () => {
      const options = {abortEarly: false};
      const {error} = Joi.validate(this.state.data, this.schema,options);
      if(!error) return null;

      const errors = {};
      for (let item of error.details) {
         errors[item.path[0]] = item.message;
      }

      console.log("Errors: ",errors);

      return errors;
   }

   handleSubmit = e => {
      e.preventDefault();

      const errors = this.validate();
      this.setState({errors: errors || {}});
      if(errors)
         return console.log("Errors: ",errors);

      this.doSubmit();
   }

   handleChange = ({currentTarget: input}) => {
      const errors = {...this.state.errors};
      const errorMessage = this.validateProperty(input);

      if(errorMessage) errors[input.name] = errorMessage;
      else delete errors[input.name]

      const data = {...this.state.data};
      data[input.name] = input.value;


      this.setState({data, errors});
   }

   renderButton(label) {
      return (<button
         className="btn btn-primary"
         disabled={this.validate()}
         onClick={(e) => this.handleSubmit(e)}>
         {label}
      </button>)
   }

   validateProperty = ({name, value}) => {
      const obj = {[name]: value};
      const schema = {[name]: this.schema[name]};

      const {error} = Joi.validate(obj, schema);

      return error ? error.details[0].message : null;
   }

   renderInput(name,label,type='text'){
      const {data, errors} = this.state;

      return(
         <Input
            type={type}
            name={name}
            value={data[name]}
            label={label}
            onChange={this.handleChange}
            error={errors[name]}
         />
      )
   }

   renderSelect(name,value,label,options = []){
      const {data} = this.state;

      return(<div className={"form-group"}>
         <label htmlFor={name}>{label.toUpperCase()}</label>

         <select onChange={this.handleChange} name={name} id={name} defaultValue={data[name]} className={"form-control mb-2"}>
            {options.map((e, key) => {
               return <option key={key} value={e[value]}>{e[label]}</option>;
            })}
         </select>
      </div>)
   }
}
